<!--  | created by JanvierCheng -->
<template>
  <div id="sampleSiteInfoDiv">
      <my-header :log="true"></my-header>
      <div class="ho-content">
          <div class="ho-nav" v-if="switchNV">
              <my-location title=""></my-location>
              <my-navigat activ="1-1"></my-navigat>
          </div>
          <div class="ho-cotet" :style="cotDivWidth">
              <div class="inputGp" v-show="switchSH">
                  <el-form ref="formSamInfo" :model="formSamInfo" label-width="70px" :inline="true" class="demo-form-inline">
                      <el-form-item label="" prop="CLOCATE">
                          <el-input v-model="formSamInfo.CLOCATE" size="small"></el-input>
                      </el-form-item>
                      <el-form-item label="" prop="ADDRESE">
                          <el-input v-model="formSamInfo.ADDRESE" size="small"></el-input>
                      </el-form-item>
                      <el-form-item label="" prop="TEL">
                          <el-input v-model="formSamInfo.TEL" size="small"></el-input>
                      </el-form-item>
                      <el-form-item label="" prop="MEMENBER">
                          <el-input v-model="formSamInfo.MEMENBER" size="small"></el-input>
                      </el-form-item>
                      <el-form-item label="" prop="YMODEL">
                          <el-input v-model="formSamInfo.YMODEL" size="small"></el-input>
                      </el-form-item>
                      <el-form-item label="" prop="PROVINCE">
                          <el-input v-model="formSamInfo.PROVINCE" size="small"></el-input>
                      </el-form-item>
                  </el-form>
              </div>
              <div class="naigatDIV">
                  <el-switch v-model="switchNV" on-text="" off-text="" :width="60">
                  </el-switch>
              </div>
              <div class="switchDIV">
                  <el-switch v-model="switchSH" on-text="" off-text="" :width="60">
                  </el-switch>
              </div>
              <div class="operatBtnG">
                    <el-button-group>
                      <el-button type="primary" icon="plus" @click="onAdd"></el-button>
                      <el-button type="primary" icon="edit" @click="onEdit"></el-button>
                      <el-button type="primary" icon="delete" @click="onDelete"></el-button>
                    </el-button-group>
                    <el-button-group>
                      <router-link to="/sampleSiteInfo/samSiteImpot"><el-button type="primary" icon="upload" style="border-radius: 5px 0 0 5px !important;">Excel</el-button></router-link>
                      <el-button type="primary" icon="d-caret" @click="onExport">Excel</el-button>
                    </el-button-group>
                    <el-button-group>
                      <el-button icon="search" @click="getList"></el-button>
                      <el-button icon="close" @click="resetForm('formSamInfo')"></el-button>
                    </el-button-group>
              </div>
              <div class="tableDIV">
                  <el-table
                    :data="tableData"
                    border
                    style="width: 100%"
                    max-height = "600"
                    v-loading.body="loading2"
                    element-loading-text=""
                    @selection-change="handleSelectionChange">
                    <el-table-column
                      type="selection"
                      width="35">
                    </el-table-column>
                    <el-table-column
                      prop="TB_CaiYang"
                      label=""
                      width="200"
                      :show-overflow-tooltip="true">
                    </el-table-column>
                    <el-table-column
                      prop="TB_Province"
                      label=""
                      width="65">
                    </el-table-column>
                    <el-table-column
                      prop="TB_Addres"
                      label=""
                      width="230"
                      :show-overflow-tooltip="true">
                    </el-table-column>
                    <el-table-column
                      prop="TB_Man"
                      label=""
                      width="100">
                    </el-table-column>
                    <el-table-column
                      prop="TB_Tel"
                      label=""
                      width="100">
                    </el-table-column>
                    <el-table-column
                      prop="TB_SiYang"
                      label=""
                      width="82">
                    </el-table-column>
                    <el-table-column
                      prop="TB_GuiMo"
                      label=""
                      width="80">
                    </el-table-column>
                    <el-table-column
                      prop="TB_YModel"
                      label=""
                      width="100">
                    </el-table-column>
                    <el-table-column
                      prop="BuildDate"
                      label=""
                      width="100">
                    </el-table-column>
                    <el-table-column
                      prop="TB_Remark"
                      label=""
                      width="400">
                    </el-table-column>
                  </el-table>
              </div>
              <div class="block">
                <el-pagination
                  @size-change="handleSizeChange"
                  @current-change="handleCurrentChange"
                  :current-page="currentPage"
                  :page-count = "totalPage"
                  :page-sizes="[100, 200, 300, 400]"
                  :page-size="pageSize"
                  layout="total, sizes, prev, pager, next, jumper">
                </el-pagination>
              </div>
              <el-dialog title="" v-model="dialogFormVisible">
                  <el-form :model="formAdd" :rules="formAddRules" ref="formAdd" :inline="true">
                    <el-form-item label="" :label-width="formLabelWidth" prop="caiY">
                      <el-input v-model="formAdd.caiY" auto-complete="off" :disabled="!currentOper"></el-input>
                    </el-form-item>
                    <el-form-item label="" :label-width="formLabelWidth" prop="provc">
                      <el-select  v-model="formAdd.provc" placeholder="">
                          <el-option :value="prvnc.name" v-for="prvnc in provc"></el-option>
                      </el-select>
                    </el-form-item>
                    <el-form-item label="" :label-width="formLabelWidth" prop="addres">
                      <el-input v-model="formAdd.addres" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="" :label-width="formLabelWidth" prop="man">
                      <el-input v-model="formAdd.man" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="" :label-width="formLabelWidth" prop="tel">
                      <el-input v-model="formAdd.tel" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="" :label-width="formLabelWidth" prop="siya">
                      <el-input v-model="formAdd.siya" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="" :label-width="formLabelWidth" prop="guimo">
                      <el-input v-model="formAdd.guimo" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="" :label-width="formLabelWidth" prop="moshi">
                      <el-select  v-model="formAdd.moshi" placeholder="">
                          <el-option :value="moshi" v-for="moshi in yangModel"></el-option>
                      </el-select>
                    </el-form-item>
                    <el-form-item label="" :label-width="formLabelWidth" prop="date1">
                        <el-date-picker type="date" placeholder="" v-model="formAdd.date1" :editable="false"></el-date-picker>
                    </el-form-item>
                    <el-form-item label="" :label-width="formLabelWidth" prop="remark">
                      <el-input v-model="formAdd.remark" auto-complete="off"></el-input>
                    </el-form-item>
                  </el-form>
                  <div slot="footer" class="dialog-footer">
                    <el-button @click="resetForm('formAdd')"> </el-button>
                    <el-button type="primary" @click="onConfirm"> </el-button>
                  </div>
              </el-dialog>
              <div class="clearfix"></div>
          </div>
      </div>
  </div>
</template>

<script>
import myHeader from './../components/header'
import myLocation from './../components/location'
import myNavigat from './../components/navigat'
import axios from 'axios'
import dataSet from './../publicAPI/dataSet'
import dataRules from './../publicAPI/dataRules'
import config from './../publicAPI/config'
// import myInput from './components/inputGroup'

export default {
  components: {
      myHeader,
      myLocation,
      myNavigat,
    //   myInput
  },
  data(){
      return{
          rootURL: config.rootURL,
          tableData: [],
          multipleSelection: [],
          formSamInfo: {
              CLOCATE: '',
              ADDRESE: '',
              TEL: '',
              MEMENBER: '',
              YMODEL: '',
              PROVINCE: ''
          },
          formAdd:{
              caiY: '',
              provc: '',
              man: '',
              tel: '',
              siya: '',
              guimo: '',
              moshi: '',
              date1: '',
              remark: '',
          },
          formAddRules:{
              caiY: [
                   { required: true, message: '', trigger: 'blur' },
                   { min: 1, max: 50, message: ' 1  50 ', trigger: 'blur' }
              ],
              provc: [
                   { required: true, message: '', trigger: 'change' },
              ],
              tel:[
                   { min: 1, max: 20, message: ' 1  20 ', trigger: 'blur' }
              ]
          },
          totalPage: 0, //
          pageSize: 100, //
          provc: dataSet.province,
          yangModel: dataSet.yangModel,
          switchSH: true,   //
          switchNV: true,   //
          loading2: true,    //
          dialogFormVisible: false,
          formLabelWidth: '100px',
          currentPage: 1,   //
          currentOper: true,  //true,false
          cotDivWidth:{
              width: '1150px',
          }
      }
  },
  methods:{
        onConfirm(){//
            let that = this
            this.$refs['formAdd'].validate((valid) => {
                  if (valid) {
                      var param = {
                          farmname: that.formAdd.caiY ,
                          farmprovince: that.formAdd.provc ,
                          farmlinkman: that.formAdd.man ,
                          farmphone: that.formAdd.tel ,
                          farmraiseway: that.formAdd.siya ,
                          farmraisescope: that.formAdd.guimo ,
                          farmraisetype: that.formAdd.moshi ,
                          farmbuilddate: dataRules.formatDate(that.formAdd.date1) ,
                          farmremarks: that.formAdd.remark ,
                      }
                      var str = JSON.stringify(param)
                      if(this.currentOper){ //
                          axios.post(that.rootURL+'/farm/addition', param)
                          .then(function (res) {
                              if(res.data.state){
                                  that.$message({
                                    type: 'success',
                                    message: res.data.value
                                  });
                                  that.dialogFormVisible = false
                                  that.getList()
                              }else{
                                  that.$message({
                                    type: 'error',
                                    message: res.data.value
                                  });
                              }
                          })
                          .catch(function (error) {
                          });
                      }else{ //
                          axios.post(that.rootURL+'/farm/update', param)
                          .then(function (res) {
                              if(res.data.state){
                                  that.$message({
                                    type: 'success',
                                    message: res.data.value
                                  });
                                  that.formAdd = []
                                  that.dialogFormVisible = false
                                  that.getList()
                              }else{
                                  that.$message({
                                    type: 'error',
                                    message: res.data.value
                                  });
                              }
                          })
                          .catch(function (error) {
                          });
                      }
                  } else {
                    console.log('error submit!!');
                    return false;
                  }
            });

        },
        onAdd(){//
            this.currentOper = true
            this.dialogFormVisible = true
        },
        onEdit(){//
            if(this.multipleSelection.length==1){
                this.currentOper = false
                this.dialogFormVisible = true
                this.formAdd = {
                    caiY: this.multipleSelection[0].TB_CaiYang,
                    provc: this.multipleSelection[0].TB_Province,
                    man: this.multipleSelection[0].TB_Man,
                    tel: this.multipleSelection[0].TB_Tel,
                    siya: this.multipleSelection[0].TB_SiYang,
                    guimo: this.multipleSelection[0].TB_GuiMo,
                    moshi: this.multipleSelection[0].TB_YModel,
                    date1: this.multipleSelection[0].BuildDate,
                    remark: this.multipleSelection[0].TB_Remark,
                }
            }
        },
        onDelete(){//
            if(this.multipleSelection.length>0){
                let that = this;
                this.$confirm(', ?', '', {
                  confirmButtonText: '',
                  cancelButtonText: '',
                  type: 'warning'
                }).then(() => {
                    let str = "";
                    for(var item of this.multipleSelection){
                        str = str + item.TB_CaiYang + ','
                    }
                    axios.post(that.rootURL+'/farm/deletion', {
                       farmname: str
                    })
                    .then(function (res) {
                        if(res.data.state){
                            that.$message({
                              type: 'success',
                              message: res.data.value
                            });
                            that.getList();
                            // that.splice(
                        }else{
                            that.$message({
                              type: 'error',
                              message: res.data.value
                            });
                        }
                    })
                    .catch(function (error) {
                    });

                }).catch((error) => {
                    // this.$message.error(error);
                });
            }
        },
        onExport(){
            let that = this;
            if((this.multipleSelection).length > 0){
                var caiYStr = '';
                for ( var item of this.multipleSelection){
                    caiYStr = caiYStr + item.TB_CaiYang + ','
                }
                console.log(caiYStr)
                axios.get(that.rootURL + '/farm/download?farmname=' + caiYStr)
                    .then((res) => {
                        window.open(that.rootURL + '/farm/download?farmname=' + caiYStr)
                    })
                    .catch((error) =>{
                        console.error(error);
                    })
            }else{
                this.$message.error('');
            }

        },
        handleSelectionChange(val) {
            this.multipleSelection = val;
        },
        // onSearch(){
        //
        // },
        getList(){
            let that = this;
            var param = {
                farmaddress: that.formSamInfo.ADDRESE,
                farmlinkman: that.formSamInfo.MEMENBER,
                farmname: that.formSamInfo.CLOCATE,
                farmphone: that.formSamInfo.TEL,
                farmprovince: that.formSamInfo.PROVINCE,
                farmraisetype: that.formSamInfo.YMODEL,
                order: '',
                page: that.currentPage,
                rows: that.pageSize,
                sort: 'desc'
            }
            axios.post(that.rootURL+'/farm/list', param)
            .then(function (res) {
                that.tableData = [];
                that.totalPage = res.data.value.pages
                for( var item of res.data.value.data){
                    that.tableData.push({
                        TB_CaiYang: item.farmname,
                        TB_Province: item.farmprovince,
                        TB_Addres: item.farmaddress,
                        TB_Man: item.farmlinkman,
                        TB_Tel: item.farmphone,
                        TB_SiYang: item.farmraiseway,
                        TB_GuiMo: item.farmraisescope,
                        TB_YModel: item.farmraisetype,
                        BuildDate: item.farmbuilddate,
                        TB_Remark: item.farmremarks
                    })
                }
                that.loading2 = false;
            })
            .catch(function (error) {
              console.error(error);
            });
        },
        resetForm(formName) {
            this.$refs[formName].resetFields();
            if(formName == 'formSamInfo'){
                this.formSamInfo.date1 = "";
                this.formSamInfo.date2 = "";
                this.getList();
            }else if(formName == 'formAdd'){
                this.formAdd =  {
                    caiY: '',
                    provc: '',
                    man: '',
                    tel: '',
                    siya: '',
                    guimo: '',
                    moshi: '',
                    date1: '',
                    remark: '',
                }
                this.dialogFormVisible = false
            }
        },
        handleSizeChange(val) {
            this.pageSize = val;
            this.getList();
            console.log(` ${val} `);
        },
        handleCurrentChange(val) {
            this.currentPage = val;
            this.getList();
            console.log(`: ${val}`);
        }
  },
  created(){
      this.getList();
  },
  watch:{
      switchNV(val){
          if(val){
             this.cotDivWidth = {
                  width: '1150px',
             }
         }else{
             this.cotDivWidth = {
                  width: '100%',
             }
         }
     },
    //  dialogFormVisible(val){
    //      if(!val){
    //          this.$refs['formAdd'].resetFields();
    //      }
    //  }
  }
}
</script>

<style scoped>
.ho-cotet{
    /*width: ;*/
    float: right;
    position: relative;
    padding: 30px 30px 50px;
}
/**/
.inputGp{
    margin-bottom: 20px;
}
.inputGp .el-form-item{
    margin-bottom: 2px;
}
.inputGp .el-input{
    width: 200px;
    margin: 0;
}
.el-button{
    padding: 9px 14px;
}
/**/
.operatBtnG{
    margin-bottom: 30px;
}
.tableDIV{
    /*width: 1030px;*/
}
/**/
.sep1150{
    width: 1150px !important;
}
.sep100per{
    width: 100% !important;
}
/**/
.block{
    margin-top: 40px;
}
/**/
.el-dialog .el-form-item .el-input, .el-dialog .el-form-item .el-select{
    width: 180px ;
}
</style>



