<!--  | created by JanvierCheng -->
<template>
  <div id="drugResistanceDIV">
      <my-header :log="true"></my-header>
      <div class="ho-content">
          <div class="ho-nav" v-if="switchNV">
              <my-location title="-"></my-location>
              <my-navigat activ="2-2"></my-navigat>
          </div>
          <div class="ho-cotet" :style="cotDivWidth">
              <div class="inputGp" v-if="switchSH">
                  <el-form ref="formSamInfo" :model="formSamInfo" label-width="70px" :inline="true" class="demo-form-inline">
                      <el-form-item label="" prop="id">
                          <el-input v-model="formSamInfo.id"></el-input>
                      </el-form-item>
                      <el-form-item label="" prop="inno">
                          <el-input v-model="formSamInfo.inno"></el-input>
                      </el-form-item>
                      <el-form-item label="" prop="caiyangdi">
                          <el-input v-model="formSamInfo.caiyangdi"></el-input>
                      </el-form-item>
                      <el-form-item label="" prop="yangzhi">
                          <el-input v-model="formSamInfo.yangzhi"></el-input>
                      </el-form-item>
                      <el-form-item label="" prop="age">
                          <el-input v-model="formSamInfo.age"></el-input>
                      </el-form-item>
                      <el-form-item label="" prop="province">
                          <el-select  v-model="formSamInfo.province" multiple placeholder="">
                              <el-option :value="prvnc.name" v-for="prvnc in prvncs"></el-option>
                          </el-select>
                      </el-form-item>
                      <el-form-item label="" prop="junshu">
                          <el-select  v-model="formSamInfo.junshu" multiple placeholder="">
                              <el-option :value="item" v-for="item in junshuSelect"></el-option>
                          </el-select>
                      </el-form-item>
                      <el-form-item label="" prop="animal">
                          <el-select  v-model="formSamInfo.animal" multiple placeholder="">
                              <el-option :value="item.animalname" v-for="item in animalSelect"></el-option>
                          </el-select>
                      </el-form-item>
                      <el-form-item label="" prop="part">
                          <el-select  v-model="formSamInfo.part" multiple placeholder="">
                              <el-option :value="item.partname" v-for="item in partSelect"></el-option>
                          </el-select>
                      </el-form-item>
                      <el-form-item label="" prop="source">
                          <el-select  v-model="formSamInfo.source" multiple placeholder="">
                              <el-option :value="item.sourcename" v-for="item in sourceSelect"></el-option>
                          </el-select>
                      </el-form-item>
                      <el-form-item label="">
                          <el-date-picker type="date" placeholder="" v-model="formSamInfo.date1" :editable="false" style="width: 144px !important;"></el-date-picker>
                          -&nbsp;<el-date-picker type="date" placeholder="" v-model="formSamInfo.date2" :editable="false" style="width: 144px !important;"></el-date-picker>
                      </el-form-item>
                      <el-form-item label="" prop="DRUGRANGE">
                          <el-select  v-model="formSamInfo.DRUGRANGE" placeholder="" style="width:120px !important;">
                              <el-option :value="drug" v-for="drug in allDrugSet"></el-option>
                          </el-select>
                          &nbsp;&nbsp;
                          <el-input v-model="formSamInfo.DRUGRANGE_S" style="width:56px !important;"></el-input>
                          &nbsp;&nbsp;
                          <el-input v-model="formSamInfo.DRUGRANGE_E" style="width:56px !important;"></el-input>

                      </el-form-item>
                      <el-button icon="plus" @click="addRange"></el-button>
                  </el-form>
                  <myRange :items="add_range"></myRange>
              </div>
              <div class="naigatDIV">
                  <el-switch v-model="switchNV" on-text="" off-text="" :width="60">
                  </el-switch>
              </div>
              <div class="switchDIV">
                  <el-switch v-model="switchSH" on-text="" off-text="" :width="60">
                  </el-switch>
              </div>
              <div class="operatBtnG">
                  <el-button-group>
                    <el-button type="primary" icon="d-caret"></el-button>
                    <el-button type="primary" icon="more" @click="toAnalyse"></el-button>
                  </el-button-group>
                  <el-button-group>
                    <el-button icon="search" @click="getList"></el-button>
                    <el-button icon="close" @click="resetForm('formSamInfo')"></el-button>
                  </el-button-group>
              </div>
              <div class="tableDIV">
                  <el-table
                    :data="tableData"
                    border
                    style="width: 100%"
                    max-height = "800"
                    v-loading.body="loading2"
                    element-loading-text=""
                    @selection-change="handleSelectionChange">
                    <el-table-column
                      type="selection"
                      width="35" >
                    </el-table-column>
                    <el-table-column
                      prop="TB_strainno"
                      label=""
                      fixed
                      width="120">
                    </el-table-column>
                    <el-table-column
                      prop="TB_inno"
                      label=""
                      fixed
                      width="160">
                    </el-table-column>
                    <el-table-column
                      prop="TB_sampleno"
                      label=""
                      fixed
                      width="106">
                    </el-table-column>
                    <el-table-column
                      prop="TB_caiyangtime"
                      label=""
                      width="120">
                    </el-table-column>
                    <el-table-column
                      prop="TB_province"
                      label=""
                      width="80">
                    </el-table-column>
                    <el-table-column
                      prop="TB_caiyangdi"
                      label=""
                      width="240">
                    </el-table-column>
                    <el-table-column
                      prop="TB_yangzhi"
                      label=""
                      width="300">
                    </el-table-column>
                    <el-table-column
                      prop="TB_animal"
                      label=""
                      width="80">
                    </el-table-column>
                    <el-table-column
                      prop="TB_age"
                      label=""
                      width="80">
                    </el-table-column>
                    <el-table-column
                      prop="TB_source"
                      label=""
                      width="80">
                    </el-table-column>
                    <el-table-column
                      prop="TB_part"
                      label=""
                      width="200">
                    </el-table-column>
                    <el-table-column
                      prop="TB_caiyangman"
                      label=""
                      width="180">
                    </el-table-column>
                    <el-table-column
                      prop="TB_history"
                      label=""
                      width="400">
                    </el-table-column>
                    <el-table-column
                      prop="TB_yangpinremark"
                      label=""
                      width="100">
                    </el-table-column>
                    <el-table-column
                      prop="TB_junshu"
                      label=""
                      width="100">
                    </el-table-column>
                    <el-table-column
                      prop="TB_junzhong"
                      label=""
                      width="130">
                    </el-table-column>
                    <el-table-column
                      prop="TB_savetime"
                      label=""
                      width="100">
                    </el-table-column>
                    <el-table-column
                      prop="TB_xueqing"
                      label=""
                      width="100">
                    </el-table-column>
                    <el-table-column
                      prop="TB_mlst"
                      label="MLST"
                      width="80">
                    </el-table-column>
                    <el-table-column
                      prop="TB_plg"
                      label="PLG"
                      width="80">
                    </el-table-column>
                    <el-table-column
                      prop="TB_identifyman"
                      label=""
                      width="180">
                    </el-table-column>
                    <el-table-column
                      prop="TB_analyseman"
                      label=""
                      width="180">
                    </el-table-column>
                    <el-table-column
                      prop="TB_strainremark"
                      label=""
                      width="260">
                    </el-table-column>
                    <!--allDrug-->
                    <el-table-column
                      :prop="item"
                      :label="item"
                      width="52"
                      v-for="item in allSeq">
                    </el-table-column>
                    <!--allDrug-->
                    <el-table-column
                      prop="TB_shiyanman"
                      label=""
                      width="180">
                    </el-table-column>
                    <el-table-column
                      prop="TB_shiyanstyle"
                      label=""
                      width="180">
                    </el-table-column>
                    <el-table-column
                      prop="TB_micremark"
                      label="MIC"
                      width="180">
                    </el-table-column>
                    <el-table-column
                      prop="TB_resisgene"
                      label=""
                      width="180">
                    </el-table-column>
                    <el-table-column
                      prop="TB_charu"
                      label=""
                      width="180">
                    </el-table-column>
                    <el-table-column
                      prop="TB_copy"
                      label=""
                      width="180">
                    </el-table-column>
                    <el-table-column
                      prop="TB_jiehe"
                      label=""
                      width="180">
                    </el-table-column>
                    <el-table-column
                      prop="TB_operator"
                      label=""
                      width="180">
                    </el-table-column>
                  </el-table>
              </div>
              <div class="block">
                <el-pagination
                  @size-change="handleSizeChange"
                  @current-change="handleCurrentChange"
                  :current-page="currentPage"
                  :page-count = "totalPage"
                  :page-sizes="[50, 100, 200, 300, 400]"
                  :page-size="pageSize"
                  layout="total, sizes, prev, pager, next, jumper">
                </el-pagination>
              </div>
              <div class="clearfix"></div>
          </div>
          <div class="clearfix"></div>
      </div>
  </div>
</template>

<script>
import myHeader from './../components/header'
import myLocation from './../components/location'
import myNavigat from './../components/navigat'
import dataSet from './../publicAPI/dataSet'
import myRange from './../components/addDrugRange'
import axios from 'axios'
import config from './../publicAPI/config'
import dataRules from './../publicAPI/dataRules'

export default {
  components: {
      myHeader,
      myLocation,
      myNavigat,
      myRange
  },
  data() {
      return {
          URL: config.rootURL,
          tableData: [],
          multipleSelection: [],
          formSamInfo: {
              id: "",
              inno: "",
              province: [],
              caiyangdi: "",
              yangzhi: "",
              junshu: [],
              animal: [],
              part: [],
              source: [],
              age: "",
              date1: "",
              date2: "",
              DRUGRANGE: '',
              DRUGRANGE_S: '',
              DRUGRANGE_E: '',
          },
          add_range: [],
          switchSH: false,   //
          switchNV: true,   //
          totalPage: 0, //
          pageSize: 50, //
          loading2: false,    //
          cotDivWidth:{
              width: '1150px',
          },
          dialogFormVisible: false,
          formLabelWidth: '85px',
          currentPage: 1,   //
          currentOper: true,  //true,false
          prvncs: dataSet.province,
          partSelect: [],
          animalSelect: [],
          sourceSelect: [],
          junshuSelect: [],
          allDrugSet: [],
          allSeq: [],
      };
  },
  methods:{
      handleSelectionChange(val) {
          this.multipleSelection = val;
      },
      toAnalyse(){
          let that = this;
          this.formSamInfo.addRange = this.add_range;
          localStorage.setItem('drugResistance', JSON.stringify(that.formSamInfo));
          this.$router.push({path: '/analyseDrugResis'})
      },
      addRange(){ //
          for(var item of this.add_range){
              if(this.formSamInfo.DRUGRANGE == item.R){
                  this.$message.error('');
                  return false;
              }
          }
          if(dataRules.serDrugRule(this.formSamInfo.DRUGRANGE, this.formSamInfo.DRUGRANGE_S, this.formSamInfo.DRUGRANGE_E)){
              var that = this;
              this.add_range.push({
                  R:this.formSamInfo.DRUGRANGE,  S:this.formSamInfo.DRUGRANGE_S, E:this.formSamInfo.DRUGRANGE_E
              })
          }else{this.$message.error('(0-2048)');}
      },
      getSelect(){
          let that = this
          axios.all([axios.get(that.URL + '/animal/selection'),
                     axios.get(that.URL + '/source/selection'),
                     axios.get(that.URL + '/part/selection'),
                     axios.post(that.URL + '/strainToNotes/category/selection')])
              .then(axios.spread((animalres, sourceres, partres, junshures) => {
                  if(animalres.data.state){
                      that.animalSelect = animalres.data.value
                  }
                  if(sourceres.data.state){
                      that.sourceSelect = sourceres.data.value
                  }
                  for(var item of partres.data.value){
                      if(item.partname == ""){
                          that.partSelect.push({
                              partno: item.partno,
                              partname: " "
                          })
                      }else{
                          that.partSelect.push({
                              partno: item.partno,
                              partname: item.partname
                          })
                      }
                  }
                  if(junshures.data.state){
                      that.junshuSelect = junshures.data.value
                  }
              }))
      },
      getList(){
          let that = this
          var param = {
              strainno: that.formSamInfo.id,
              strainalias: that.formSamInfo.inno,
              province: that.formSamInfo.province,    //[]
              farmname: that.formSamInfo.caiyangdi,
              farmaddr: that.formSamInfo.yangzhi,
              category: that.formSamInfo.junshu,  //[]
              animalName: that.formSamInfo.animal, //[ - ]
              partName: that.formSamInfo.part,  //[ - ]
              source: that.formSamInfo.source,    //[]
              animalAge: that.formSamInfo.age,
              beginDate: that.formSamInfo.date1,
              endDate: that.formSamInfo.date2,
              page: that.currentPage,
              rows: that.pageSize,
          }
          for( var item of that.add_range){
              param[(item.R).toLowerCase()+'1'] = parseFloat(item.S)
              param[(item.R).toLowerCase()+'2'] = parseFloat(item.E)
          }

          //table
          if((that.add_range).length>=1){
              that.allSeq = [];
              (that.add_range).forEach((range, i) => {
                  (that.allSeq)[i] = range.R;
              });
              var tmp = [];
              for(var item of dataSet.yangDrug){
                  tmp.push(item);
              }
              (that.add_range).forEach((range, i) => {
                  tmp.forEach((val, j) => {
                      if(val == range.R){
                          tmp.splice(j,1)
                      }
                  })
              });
              tmp.forEach((val, j) => {
                  (that.allSeq).push(val);
              });
          }else{
              that.allSeq = dataSet.allDrug
              // console.warn(dataSet.yangDrug)
          }

        // console.warn(JSON.stringify(param))
          this.loading2 = true
          axios.post( that.URL + '/druganalysis/all/list', param )
              .then((res) => {
                  that.tableData = [];
                  that.totalPage = res.data.value.pages
                  for( var item of res.data.value.data){
                      that.tableData.push({
                            TB_strainno: item.strainno,
                            TB_inno: item.strainalias,
                            TB_sampleno: item.sampleno,
                            TB_caiyangtime: item.sampledate,
                            TB_province: item.sampleprovince,
                            TB_caiyangdi: item.farmname,
                            TB_yangzhi: item.samplefarmaddr,
                            TB_animal: item.animalname,
                            TB_age: item.sampleanimalage,
                            TB_source: item.samplesource,
                            TB_part: item.samplecollectionpart,
                            TB_caiyangman: item.samplecollector,
                            TB_history: item.samplemedicalhistory,
                            TB_yangpinremark: item.strainremarks,
                            TB_junshu: item.straincategory,
                            TB_junzhong: item.straintype,
                            TB_savetime: item.strainstoragedate,
                            TB_xueqing: item.serotype,
                            TB_mlst: item.strainmlst,
                            TB_plg: item.strainplg,
                            TB_identifyman: item.strainoperator,
                            TB_analyseman: item.strainparter,
                            TB_strainremark: item.strainremarks,

                            AMK: item.amk,
                            AMP: item.amp,
                            APR: item.apr,
                            AZM: item.azm,
                            CFZ: item.cfz,
                            FEP: item.fep,
                            CTX: item.ctx,
                            FOX: item.fox,
                            CAZ: item.caz,
                            CRO: item.cro,
                            CQM: item.cqm,
                            CHL: item.chl,
                            CIP: item.cip,
                            TIA: item.tia,
                            CLI: item.cli,
                            CL: item.cl,
                            DOX: item.dox,
                            VAL: item.val,
                            ERY: item.ery,
                            FFC: item.ffc,
                            FOS: item.fos,
                            GEN: item.gen,
                            IMP: item.imp,
                            LEV: item.lev,
                            LZD: item.lzd,
                            MEM: item.mem,
                            MIN: item.min,
                            NAL: item.nal,
                            NEO: item.neo,
                            NET: item.net,
                            NIT: item.nit,
                            NOR: item.nor,
                            OFX: item.ofx,
                            OXA: item.oxa,
                            PEN: item.pen,
                            PIP: item.pip,
                            TZP: item.tzp,
                            RIF: item.rif,
                            STR: item.str,
                            TEC: item.tec,
                            TET: item.tet,
                            TOB: item.tob,
                            TMP: item.tmp,
                            SXT: item.sxt,
                            VAN: item.van,
                            OQX: item.oqx,

                            TB_shiyanman: item.micoperator,
                            TB_shiyanstyle: item.micdetectiontype,
                            TB_micremark: item.micremarks,
                            TB_resisgene: item.genname,
                            TB_charu: item.iseq,
                            TB_copy: item.replicon,
                            TB_jiehe: item.gentc,
                            TB_operator: item.genoperator,
                      })
                  }
                  that.loading2 = false;
              })
              .catch((error) => {
                  console.error(error);
              })
      },
      resetForm(formName) {
          this.$refs[formName].resetFields();
          if(formName == 'formSamInfo'){
              this.getList();
          }
      },
      handleSizeChange(val) {
          this.pageSize = val;
          this.getList();
          console.log(` ${val} `);
      },
      handleCurrentChange(val) {
          this.currentPage = val;
          this.getList();
          console.log(`: ${val}`);
      }
  },
  watch:{
      switchNV(val){
          if(val){
             this.cotDivWidth = {
                  width: '1150px',
             }
         }else{
             this.cotDivWidth = {
                  width: '100%',
             }
         }
     },
  },
  created(){
      for(var item of dataSet.allDrug){
          (this.allDrugSet).push(item);
          (this.allSeq).push(item);
      }
      this.getSelect();
      this.getList();
  }
}
</script>

<style scoped>
.ho-cotet{
    /*width: ;*/
    float: right;
    position: relative;
    padding: 30px 30px 50px;
}
/**/
.inputGp{
    margin-bottom: 20px;
}
.inputGp .el-form-item{
    margin-bottom: 6px;
}
.inputGp .el-input, .inputGp .el-select{
    width: 180px;
    margin: 0;
}
.el-button{
    padding: 9px 14px;
}
/**/
.operatBtnG{
    margin-bottom: 30px;
}
.tableDIV{
    /*width: 1030px;*/
}
/**/
.sep1150{
    width: 1150px !important;
}
.sep100per{
    width: 100% !important;
}
/**/
.block{
    margin-top: 40px;
}
.el-dialog .el-form-item{
    margin-bottom: 20px !important;
}
.el-dialog .el-form-item .el-input, .el-dialog .el-form-item .el-select{
    width: 180px ;
}
</style>



